import * as React from 'react';
import { Menu, Icon } from 'antd';
import './LeftSideBar.less';
import { Brand, Adsense } from '../../services/service-proxies';
import { isMobile } from 'react-device-detect';
import { withNamespaces } from '@client/i18n';
const SubMenu = Menu.SubMenu;

interface Props {
  topBrands: Brand[];
  adsense: Adsense[];
  t: (key: string) => string;
}
interface State {
  isOpenDropdownMenu: boolean;
}
class BaseLeftSideBar extends React.Component<Props, State> {
  state: State = {
    isOpenDropdownMenu: false,
  };

  render() {
    const translate = this.props.t;
    const renderMenuBar = this.props.topBrands.map((brand) => {
      return (
        <SubMenu
          key={brand._id}
          title={<span style={{ color: '#000', fontSize: '12px' }}>{brand.name.toUpperCase()} ({brand.totalNews})</span>}
          onTitleClick={() => window.location.href = `/danh-sach-hang/${brand.slug}`}
        >
          {brand.brandCategories.map((category: any) => (
            <Menu.Item key={category.slug}><a title={category.name} style={{fontSize: '12px'}}>{category.name} ({category.totalNews})</a></Menu.Item>
          ))}
        </SubMenu>
      );
    });
    const adsPosition2 = this.props.adsense.filter((item) => item.position === 2)[0];

    return (
      <div className={isMobile ? 'left-sidebar-mobile' : 'left-sidebar'}>
        <span className='title'>
          <b>{translate('common:list-of-brand')}</b>
          {isMobile ? <a style={{ float: 'right', color: 'white' }} onClick={() => this.setState({ isOpenDropdownMenu: !this.state.isOpenDropdownMenu })}>
            <Icon type='caret-down' />
          </a> : <div></div>}
        </span>

        {isMobile && this.state.isOpenDropdownMenu ? <Menu
          style={{
            width: '100%',
            background: '#fff',
            border: '1px solid #F4F4F4',
            borderBottom: 'none',
            boxShadow: '0px 0px 10px 0px rgba(0, 0, 0, 0.1)',
          }}
          mode={'inline'}
          onClick={(info: any) => {
            const selectedBrand = this.props.topBrands.filter((brand) => brand._id === info.keyPath[1])[0].slug;
            const selectedCategory = info.keyPath[0];
            window.location.href = `/tim-kiem?brand=${selectedBrand}&category=${selectedCategory}`;
          }}
        >
          {renderMenuBar}
        </Menu> : <div></div>}

        {!isMobile ? <Menu
          style={{
            width: '100%',
            background: '#fff',
            border: '1px solid #F4F4F4',
            borderBottom: 'none',
            boxShadow: '0px 0px 10px 0px rgba(0, 0, 0, 0.1)',
          }}
          // mode={window.innerWidth < 992 ? 'inline' : 'vertical'}
          mode={'vertical' as any}
          onClick={(info: any) => {
            const selectedBrand = this.props.topBrands.filter((brand) => brand._id === info.keyPath[1])[0].slug;
            const selectedCategory = info.keyPath[0];
            window.location.href = `/tim-kiem?brand=${selectedBrand}&category=${selectedCategory}`;
          }}
        >
          {renderMenuBar}
        </Menu> : <div></div>}

        <a href='/danh-sach-hang' title='Danh sách hãng' className='all-brand'>
          <b>{translate('common:all-brands')}</b>
        </a>

        <a href={adsPosition2 && adsPosition2.hyperlink ? adsPosition2.hyperlink : '#'}>
          <img
            src={adsPosition2 && adsPosition2.imageUrl ? adsPosition2.imageUrl : '/static/images/sidebar-1.png'}
            width='100%'
            className='image-promotion-right'
          />
        </a>
      </div>
    );
  }
}

export const LeftSideBar = withNamespaces('common')(BaseLeftSideBar);
